body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: Arial, sans-serif;
}

#root {
  width: 100%;
  height: 100%;
}

.bg-cover {
  background-size: cover;
}

.bg-center {
  background-position: center;
}

.form-container {
  background: rgba(128, 0, 128, 0.7); /* Semi-transparent purple background */
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  width: 90%;
}

.logo {
  max-width: 150px; /* Adjust the logo size */
  margin-bottom: 2rem;
}
